body {
	background-color: #eec0c6;
	background-image: linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%);

   }

.desktopNavigationWrapper {
	display: grid;
	grid-template-columns: repeat(20, 1fr);
	background-color:rgba(219, 68, 68, 0.692);
	font-weight: bold;

}   

.navLogo {
	width: 80%;
	cursor: pointer;
	grid-column: 2/3;
	padding: 10%;
}

.shopButton,
.galleryButton,
.newsButton,
.signInButton ,
.authUserNav
{   justify-self: center;
	align-self: center;
	cursor: pointer;
	padding: 2%;
	text-transform: uppercase;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri,  'Trebuchet MS', sans-serif;
}


.shopButton {
	grid-column: 9/10;
}



.signInButton {
	grid-column: 18/20;
}    

.authUserNav {
	grid-column: 18/20;
}    


